.product {
  padding-top: 64px;

  font-size: 16px;
  font-weight: 700;
  line-height: 16px;

  &__photo {
    height: 320px;
    margin-bottom: 32px;

    transition: all 0.5s;

    @include on744px {
      height: 387px;
    }

    @include on320px {
      height: 277px;
    }

    &:hover {
      transform: scale(120%);
    }

  }

  &__category {
    width: 100%;

    object-fit: cover;
    object-position: center;

    &:hover {
      transform: scale(105%);
    }

    @include on744px {
      height: 250px;
      object-fit: cover;
    }

    @include on320px {
      width: 130px;
      height: 130px;
    }
  }

  &__description {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
  }

  &__type {
    font-weight: 400;
  }

  &__type-category {
    margin-top: -18px;

    font-style: normal;
    font-weight: 700;
    font-size: 24px;

    @include on320px {
      font-size: 16px;
    }
  }

  &__title {
    text-align: left;
  }

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;

    height: 56px;

    background-color: $color-black;
    border-radius: 40px;
    text-decoration: none;
    border: 3px solid $color-black;
    color: white;
    transition: all 0.5s;

    &:hover {
      border: 3px solid $color-black;
      background-color: white;
      color: $color-black;
    }

    &:active {
      background-color: #111;
      color: white;
    }
  }

  &__item-c-1,
  &__item-c-3,
  &__item-c-6 {
    flex-basis: calc(66.6% - 24px);
  }

  &__item-c-2,
  &__item-c-4,
  &__item-c-5 {
    flex-basis: calc(33.3%);
  }

  &__item-b {
    flex-basis: 33%;
    padding-left: calc(33% + 24px);
  }

  @include on744px {
    &__item-b {
      flex-basis: 68%;
      padding-left: 16%;
    }
  }

  @include on320px {

    &__item-c-1,
    &__item-c-3,
    &__item-c-6,
    &__item-c-2,
    &__item-c-4,
    &__item-c-5 {
      flex-basis: calc(50% - 24px);
    }

    &__item-b {
      flex-basis: 100%;
      padding-left: 0;
    }
  }
}

.product-container {
  position: relative;

  box-sizing: border-box;
  width: 100%;
  padding-bottom: 0;

  @include on320px {
    width: 100%;

    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    }
  }
}
